/* Secci贸n principal */
.main {
    background-image: url('../../assets/Main/logo.png');
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: center;
    height: 100vh;
    margin-top: -55px;
    z-index: -999;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: end;
        align-items: flex-end;
    margin-left: 0px !important;
    margin-right: 0px !important;
}

/* Wallpaper de la secci贸n de caracteristicas*/
.burbujas {
    background-image: url('../../assets/Main/wallpaper.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    z-index: -99999;
}

h2, h3, h4 {
    color: #ff5400;
    text-align: center;
}

h2.cintillo {
  margin-top: 0px;
  font-family: "Quicksand";
  font-weight: bold;
  color: #ff5400;
}

/* Seccion "Trabajamos sc"*/
.caracteristicas {
    border-radius: 10px;
    background-color: white;
    margin-top: 25px;
    margin-bottom: 25px;
}

div.caracteristicas > h2 {
  font-family: "Quicksand";
  font-weight: bold;
  color: #ff5400;
  text-align: center;
}

.thumbnail {
    border: none;
}



/* Seccion bot贸n de Huellas*/
.huellas {
    padding: 25px 120px;
    color: white !important;
    background-color: #ff5400;
    margin: 0px 0px;
}

div.row.huellas {
  background-image: url('../../assets/Main/fondo-slogan.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  background-attachment: fixed;
  z-index: -99999;
}

span.boton {
    font-family: "Quicksand";
    font-weight: bold;
    margin: 0 auto;
    text-align: center;
    display: table;
    background-color: #24FF00;
    border-radius: 25px 25px;
    color: white;
    padding: 5px 10px;
}

span.boton>a{
    color: white;
    font-size: 2em;
}

span.boton>a:hover {
  text-decoration: none;
}

/* Secci贸n de Testimonios*/
.testimonio {
  margin: 0px 0px;
}

div.row.testimonio {
  background-image: url('../../assets/Main/fondo-testimonio.jpg');
  background-position: left;
  background-repeat: no-repeat;
  background-size: auto;
  background-attachment: fixed;
  z-index: -99999;
}

img.testimonio {
  padding: 35px 80px;
}

p.testimonio {
  font-size: 52px;
  color: #ff5400;
  padding-top: 70px;
  text-align: center;
  font-family: "Fredoka One";
  font-weight: normal;
}

/* Media Queries */

@media screen and (max-width: 320px) {

  .huellas {
    padding-left: 20px;
    padding-right: 20px;
  }

  p.testimonio {
    padding-top: 25px !important;
  }

  div.testimonio {
    padding-top: 0px !important;
  }

}



