.burbujas {
    background-image: url('../../assets/Main/wallpaper.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    z-index: -99999;
}

div.row.burbujas>img {
  width: 100%;
  position: relative;
  left: -20px;
  padding: 10px 0px;
}

.triangulo {
    background-image: url('../../assets/Servicios/triangulo.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -99999;
}

h4 {
  color: black;
}

.caracteristicas {
    border-radius: 10px;
    background-color: white;
    margin-top: 25px;
    margin-bottom: 25px;
}

.thumbnail {
    border: none;
}

ul {
  list-style-type: decimal;
}

img.img-responsive.pagos {
  width: 250px !important;
  padding-left: 15px;
  padding-right: 15px;
  display: inline-block;
}

p, ol {
  font-size: 18px;
}

@media screen and (max-width: 320px) {
  p, ol {
    font-size: "15px";
  }
}



